import * as React from "react";
import "./Player.css";

import ReactPlayer from "react-player";
import Controler from "./Controler";
import Text from "./Text";
import Info from "./Info";
import { Box, Grid, Stack } from "@mui/material";

import gsap from "gsap";

import tv from "../Resources/tv.png";

//Component to play the chosen trailer.

export default function Player({ results }) {
  const tvRef = React.useRef(null);
  const [lastChange, setLastChange] = React.useState(1);
  const [playing, setPlaying] = React.useState(false);
  const [index, setIndex] = React.useState(0);
  const [source, setSource] = React.useState(
    "https://www.youtube.com/watch?v=DMxOk3AIAVU"
  );

  React.useEffect(() => {
    gsap.from(tvRef.current, { opacity: 0, duration: 2 });
  }, []);

  const play = () => {
    setPlaying(true);
  };

  React.useEffect(() => {
    setSource(
      "https://www.youtube.com/watch?v=" +
        results[index].videos.results[results[index].videos.results.length - 1]
          .key
    );
  }, [index]);

  return (
    <>
      <Text
        years={results[index].release_date.slice(0, 4)}
        title={results[index].original_title}
      ></Text>
      <Box ref={tvRef} className="player-wrapper">
        <Box
          className="tv"
          sx={{
            paddingTop: "-10%",
            width: "90%",
            height: "110%",
            zIndex: 2,
          }}
          component="img"
          alt="TV"
          src={tv}
        />
        <ReactPlayer
          onError={() => {
            results.splice(index, 1);
            setIndex(index + lastChange);
          }}
          onReady={() => play()}
          config={{
            youtube: {
              playerVars: { modestbranding: 1 },
            },
          }}
          className="react-player"
          id="videoPlayer"
          playing={playing}
          loop={true}
          controls={false}
          width="65%"
          height="100%"
          url={source}
        />
      </Box>
      <Box sx={{ paddingTop: "5%", paddingBottom: "20px" }}>
        <Controler
          setLastChange={setLastChange}
          setPlaying={setPlaying}
          setIndex={setIndex}
          max={results.length - 1}
        />
      </Box>
      <Stack direction="row">
        <Info title="Information" currentFilm={results[index]} />
      </Stack>
    </>
  );
}
