import * as React from "react";
import Player from "./Player";

//Component to display the TV image and the trailer within it.

export default function TV() {
  const [results, setResults] = React.useState(null);
  const [done, setDone] = React.useState(false);
  const [complete, setComplete] = React.useState(false);

  React.useEffect(() => {
    findFilms();
  }, []);

  React.useEffect(() => {
    findTrailers();
  }, [done]);

  function compare(a, b) {
    if (a.popularity > b.popularity) {
      return -1;
    }
    if (a.popularity < b.popularity) {
      return 1;
    }
    return 0;
  }

  const findFilms = () => {
    const newDate = new Date();
    const date = newDate.getDate();
    const month = newDate.getMonth() + 1;
    const year = newDate.getFullYear();

    var temp = [];

    for (let i = 1; i <= 20; i++) {
      var dateString =
        year -
        i +
        "-" +
        (String(month).length == 1 ? "0" + month : month) +
        "-" +
        (String(date).length == 1 ? "0" + date : date);
      (async () => {
        const data = await fetch(
          "https://api.themoviedb.org/3/discover/movie?api_key=9833814c3f26a8148165da170bd2ef1f&page=1&primary_release_date.gte=" +
            dateString +
            "&primary_release_date.lte=" +
            dateString +
            "&append_to_response=videos"
        );
        const response = await data.json();
        temp = temp.concat(response.results);
        if (i == 20) {
          temp.sort(compare);
          setResults(temp.slice(0, 10));
          setDone(true);
        }
      })();
    }
  };

  const findTrailers = () => {
    var temp = [];
    if (results != null) {
      for (let i = 0; i < results.length; i++) {
        (async () => {
          const data = await fetch(
            "https://api.themoviedb.org/3/movie/" +
              results[i].id +
              "?api_key=9833814c3f26a8148165da170bd2ef1f&language=en-US&append_to_response=videos"
          );
          const response = await data.json();
          if (response.videos.results.length != 0) {
            temp.push(response);
          }
          if (i == results.length - 1) {
            setResults(temp);
            setComplete(true);
          }
        })();
      }
    }
  };
  if (complete) {
    return <Player results={results} />;
  }
}
