import "./App.css";
import React from "react";

import TV from "./Components/TV";

import { Grid, CssBaseline, Box } from "@mui/material";
import { ThemeProvider } from "@mui/material";
import { createTheme } from "@mui/material/styles";
import Intro from "./Components/Intro";

import logo from "./Resources/logo3.png";

const theme = createTheme({
  typography: {
    fontFamily: "VT323",
  },
  palette: {
    background: {
      default: "black",
    },
    text: {
      primary: "#ffffff",
    },
  },
});

function App() {
  const [loaded, setLoaded] = React.useState(false);
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />

      <Grid
        paddingTop="2%"
        container
        direction="column"
        justifyContent="center"
        alignItems="center"
      >
        <Box
          paddingTop="2%"
          component="img"
          sx={{ width: "70%", maxWidth: "600px" }}
          src={logo}
        />
        <Grid
          container
          direction="column"
          justifyContent="center"
          alignItems="center"
          sx={{
            width: { xs: "100%", md: "80%", lg: "60%", xl: "50%" },
          }}
        >
          {loaded ? <TV /> : <Intro setLoaded={setLoaded} />}
        </Grid>
      </Grid>
    </ThemeProvider>
  );
}

export default App;
