import * as React from "react";

import Box from "@mui/material/Box";
import rewind from "../Resources/rewind.png";

import gsap from "gsap";
import { Typography, IconButton, Grid } from "@mui/material";

export default function Intro({ setLoaded }) {
  const [played, setPlayed] = React.useState(false);
  const rewindRef = React.useRef(null);
  const textRef = React.useRef(null);
  const timeline = React.useMemo(() => gsap.timeline({ paused: true }), []);

  React.useEffect(() => {
    timeline
      .to(rewindRef.current, {
        rotation: -500,
        opacity: 0,
        duration: 1,
      })
      .to(textRef.current, {
        opacity: 0,
        duration: 0.5,
      });
    if (played) {
      setTimeout(() => {
        setLoaded(true);
      }, 2000);
    }
  }, [played]);

  return (
    <Grid
      container
      direction="column"
      justifyContent="center"
      alignItems="center"
    >
      <IconButton sx={{ width: "100%" }}>
        <Box
          style={{ transformOrigin: "50% 75%" }}
          ref={rewindRef}
          sx={{ paddingTop: "23%", width: "30%" }}
          onClick={() => {
            setPlayed(true);
            timeline.play();
          }}
          component="img"
          src={rewind}
        />
      </IconButton>
      <Box ref={textRef}>
        <Typography variant="body1" paddingTop="30px">
          Rewind to enter
        </Typography>
      </Box>
    </Grid>
  );
}
