import * as React from "react";

import { Grid, Typography, Stack, Box, Button, Paper } from "@mui/material";

export default function Text({ title, currentFilm }) {
  React.useEffect(() => {
    console.log(currentFilm);
  }, []);
  return (
    <Box sx={{ paddingBottom: "20px", paddingX: "20px" }}>
      <Paper
        sx={{
          width: { xs: "100%", sm: "500px" },
          backgroundColor: "#0d0d0d",
          paddingX: "20px",
          paddingY: "20px",
        }}
        elevation={"20"}
      >
        <Grid
          container
          direction="column"
          justifyContent="center"
          alignItems="flex-start"
        >
          <Typography variant="h4">{title}</Typography>
          <Typography paddingBottom="10px" variant="h6">
            {currentFilm.original_title}
          </Typography>
          <Grid
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="flex-start"
          >
            <Box
              marginRight={"5%"}
              component="img"
              width="20%"
              src={
                "https://image.tmdb.org/t/p/original/" + currentFilm.poster_path
              }
            />
            <Stack marginRight={"5%"}>
              <Typography variant="body1">Genres:</Typography>
              {currentFilm.genres.map((genre) => (
                <Typography variant="body2">{genre.name}</Typography>
              ))}
            </Stack>
            <Button
              onClick={() =>
                window.open("https://www.imdb.com/title/" + currentFilm.imdb_id)
              }
              variant="contained"
              color="warning"
            >
              IMDb
            </Button>
            <Typography marginLeft={"5%"} variant="body1">
              {"Released: " + currentFilm.release_date}
            </Typography>
            <Typography paddingTop="10px">{currentFilm.overview}</Typography>
          </Grid>
        </Grid>
      </Paper>
    </Box>
  );
}
